
body{
  padding: 0;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  overflow-y:scroll;
}
#app-body{
  background: -webkit-gradient(linear, left top, right bottom, from(rgb(254, 255, 255)) , to(rgb(137, 212, 255)));
  background: linear-gradient(to bottom right, rgb(254, 255, 255) , rgb(137, 212, 255));
}
@media only screen and (min-width: 700px){
#app{
    display: grid;
    grid-template-columns: 20% 4fr;
    max-width: 100vw;
    margin: 0 auto;
    grid-template-areas: "app-nav app-body"
}
#app-nav{
    grid-area: 'app-nav';
    width: 100%;
    height: 100vh;
    text-align: center;
}
#app-body{
    grid-area: "app-body";
    border-left: 2px solid rgb(206, 223, 255);
}
.app-about{
    margin-bottom: 10em;
}
}
@media only screen and (max-width: 700px){
body{
    margin: 0;
    padding: 0;
}
#app{
    display: grid;
    grid-template-rows: 100% 1fr;
}
#app-body{
    grid-row: 2/3;
}
#app-nav{
    grid-row: 1/2;
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100vw;
}
}

