.project_active{
    background-color: rgba(255, 255, 255, 0.596) !important;
    font-size: 1.001em;
    color: rgb(129, 129, 129) !important;
}
.projects{
    display: grid;
    grid-template-rows: minmax(100px, 1fr);
    padding: 2em 1.5em;
}
.project{
    -webkit-transition: 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition: 1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    background-color: white;
    margin: 1em 3em;
    padding: 1.5em 3em;
    -webkit-box-shadow: 1px 1px 5px 1px rgba(196, 196, 196, 0.603);
            box-shadow: 1px 1px 5px 1px rgba(196, 196, 196, 0.603);
    border: 1px solid white;
    color: rgb(134, 143, 141);
}
.project > a{
    font-style: none;
    text-decoration: none;
    color: rgb(57, 115, 161);
    padding-top: 1em
}
.project > small{
    margin-left: 2em;
}
.project > hr{
    border: 0; 
    height: 1px; 
    background: rgba(0,89,255, 0.13);
}

